/* ============================================================
BREADCRUMS SECTION
============================================================ */
.c_breadcrumbs {
  margin-top: 6.6px;
}

.c_breadcrumbs ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  width: 100%;
}

.c_breadcrumbs ul li:first-child {
  margin-left: 0 !important;
}

.c_breadcrumbs ul li:first-child a:before {
  content: normal;
}

.c_breadcrumbs ul li:last-child {
  background: none;
}

.c_breadcrumbs ul li:last-child a {
  /*color: #1d1d1d;*/
}

.c_breadcrumbs ul li:last-child:after,
.c_breadcrumbs ul li:last-child:before {
  content: normal;
}

.c_breadcrumbs ul a:hover {
  color: gray;
}

.c_breadcrumbs ul a:hover:before {
  border-color: #ddd #ddd #ddd transparent;
}

.c_breadcrumbs ul a:hover:after {
  border-left-color: #ddd;
}

@media only screen and (max-width: 767px) {
  .c_breadcrumbs li::after {
    /* this is the separator between items */
    display: inline-block;
    content: '\00bb';
    font-size: 14px;
    color: #959fa5;
    margin-top: 3px;
    margin-left: 9px;
    margin-right: 10px;
  }
  .c_breadcrumbs ul a {
    padding: .3em .1em;
    float: left;
    text-decoration: none;
    position: relative;
  }
  .c_breadcrumbs ul li {
    float: left;
    /*margin: 0 .1em 0 1em;*/
  }
  .c_breadcrumbs ul .active:hover a {
    color: #ddd;
  }
}

@media only screen and (min-width: 768px) {
  .c_breadcrumbs ul li {
    float: left;
    margin: 0 .2em 0 1em;
  }
  .c_breadcrumbs ul a {
    background: #F5F5F5;
    padding: .3em 1em;
    float: left;
    text-decoration: none;
    position: relative;
  }
  .c_breadcrumbs ul a:hover {
    background: #ddd;
    color: gray;
  }
  .c_breadcrumbs ul a:before {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -1.5em;
    border-width: 1.5em 0 1.5em 1em;
    border-style: solid;
    border-color: #F5F5F5 #F5F5F5 #F5F5F5 transparent;
    left: -1em;
    margin-left: 1px;
  }
  .c_breadcrumbs ul a:after {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -1.5em;
    border-top: 1.5em solid transparent;
    border-bottom: 1.5em solid transparent;
    border-left: 1em solid #F5F5F5;
    right: -1em;
    margin-right: 1px;
  }
  .c_breadcrumbs ul .active:hover a {
    color: #ffffff;
  }
}

.c_breadcrumbs ul .active,
.c_breadcrumbs ul .active:hover {
  background: none;
  font-weight: bold;
}

.c_breadcrumbs ul .active a,
.c_breadcrumbs ul .active:hover a {
  color: gray;
}

.c_breadcrumbs ul .active:after,
.c_breadcrumbs ul .active:before {
  content: normal;
}

.c_breadcrumbs.small li {
  margin: 0 .2em 0 .8em;
}

.c_breadcrumbs.small a {
  padding: .2em 1em;
  font-size: 11.9px;
}

.c_breadcrumbs.mini li {
  margin: 0 .2em 0 .6em;
}

